<template>
  <div>
    <div class="alert alert-primary text-center">
      Alteração da senha
    </div>
    <form class="container" ref="form" @submit.prevent="submit">
      <div class="card mb-3">
        <div class="card-body">
          <div class="form-group">
            <label for="ipt-senhanova">Nova senha</label>
            <input id="ipt-senhanova" type="password" class="form-control"
                   minlength="6"
                   maxlength="25"
                   autocapitalize="off"
                   required
                   :class="{
                   'is-valid': iptSenhaNova.length>=6 && iptSenhaNova.indexOf(' ') === -1,
                   'is-invalid': (iptSenhaNova.length>0 && iptSenhaNova.length<6) || iptSenhaNova.indexOf(' ') !== -1
                   }"
                   :disabled="loading"
                   v-model="iptSenhaNova">
            <span class="invalid-feedback" v-show="iptSenhaNova.length>0 && iptSenhaNova.length<6">Tamanho mínimo de 6 caracteres</span>
            <span class="invalid-feedback" v-show="iptSenhaNova.indexOf(' ') !== -1">Não use espaço em branco</span>
          </div>
          <div class="form-group">
            <label for="ipt-senhanova2">Repita a nova senha</label>
            <input id="ipt-senhanova2" type="password" class="form-control"
                   autocapitalize="off"
                   required
                   :class="{
                   'is-valid': (iptSenhaNova===iptSenhaNova2) && iptSenhaNova,
                   'is-invalid': (iptSenhaNova!==iptSenhaNova2) && iptSenhaNova
                   }"
                   :disabled="loading"
                   v-model="iptSenhaNova2">
          </div>
        </div>
        <div class="card-footer">
          <button ref="btnsubmit" class="btn btn-block btn-primary" type="submit" :disabled="!valido||loading">
            <span v-if="!loading">CONFIRMAR</span>
            <span v-else class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
          </button>
          <router-link class="btn btn-block btn-secondary" to="/">VOLTAR</router-link>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    name: "ChangePassword",
    data: () => ({
      iptSenhaNova: '',
      iptSenhaNova2: '',
      loading: false
    }),
    methods: {
      async submit() {
        if (!this.valido) {
          this.$toastr.warning('Corrija a senha');
          return;
        }
        this.loading = true;
        let formData = new FormData();
        formData.append("senhanova", this.iptSenhaNova);
        try {
          await this.$http.appApi.post('/usuarios/mudarsenha.php', formData, {headers: {'Authorization': this.$store.state.token}});
          this.$toastr.success('Realize um novo acesso', 'Senha alterada');
          await this.$store.dispatch('signout');
        } catch (e) {
          this.loading = false;
        }
      }
    },
    computed: {
      valido() {
        return this.iptSenhaNova.length>=6 && this.iptSenhaNova.indexOf(' ') === -1 && this.iptSenhaNova === this.iptSenhaNova2;
      }
    }
  }
</script>

